.name {
    text-align: center;
    font-size: 25px;
    margin-top: -20%;
}

.logo img{
    position: absolute;
    height: 150px;
    left: 15px;
    top: 105px;

}

.braveHeartQuote{
    position: relative;
    font-size: 45px;
    padding: 0 2em;
    margin-bottom: 2em;
}


.appTitle{
    font-size: 50px;
    color: rgb(5, 88, 197);
    text-shadow: #000 0px 3px 1px;
}

.clockDays, .clockHours, .clockMinutes, .clockSeconds {
    display: inline;
    margin: 5px;
    color: rgb(5, 88, 197);
    text-shadow: #000 0px 1px 1px;
}

.clockDays {
    font-size: 35px;
}

@media (max-width: 1180px) {
    .appTitle{
        font-size: 150px;
    }
    }

@media (max-width: 980px) {
    .appTitle{
        font-size: 100px;
    }
    }
    

@media (max-width: 580px) {
    .appTitle{
        font-size: 60px;
    }
    }

    @media (max-width: 400px) {
        .appTitle{
            font-size: 50px;
        }
        }

@media (max-width: 580px) {
.clockHours, .clockMinutes, .clockSeconds{
    font-size: 15px;
}
}

@media (max-width: 580px) {
.braveHeartQuote{  
          font-size: 25px;
    }
    }

@media (max-width: 780px)
{.App-header {
  background-size: 1300px 1050px;
}}

@media (max-width: 320px) {
 .clockHours, .clockMinutes, .clockSeconds{
  font-size: 11px;
}}
